import React, { Component, Fragment } from 'react';
import Container from 'react-bootstrap/Container';
import Button from 'react-bootstrap/Button';
import InputGroup from 'react-bootstrap/InputGroup'
import Form from 'react-bootstrap/Form'
import FormControl from 'react-bootstrap/FormControl';
import Kinto from 'kinto';

class BirthdaySubmission extends Component {

  state = {
    firstName: '',
    lastName: '',
    day: '',
    month: '',
    year: '',
  }

  render() {
    return (
      <Fragment>
        <Container style={{
          display: 'flex',
          alignItems: 'center',
          flexDirection: 'column',
          justifyContent: 'center',
          // maxWidth: '100%'
        }}>
          <h1 className='m-4' style={{
            fontSize: '40px'
          }}>Birthday Submission</h1>
          <Container style={{
            width: '30vw',
            minWidth: '400px',
            textAlign: 'center'
          }}>
            <InputGroup className='mb-3'>
              <FormControl
                placeholder='First Name'
                aria-label='First Name'
                aria-describedby='basic-addon1'
                onChange={(event) => {
                  this.setState({ firstName: event.target.value });
                }}
              />
            </InputGroup>
            <InputGroup className='mb-3'>
              <FormControl
                placeholder='Last Name'
                aria-label='Last Name'
                aria-describedby='basic-addon1'
                onChange={(event) => {
                  this.setState({ lastName: event.target.value });
                }}
              />
            </InputGroup>

            <Form.Label htmlFor='basic-url'>Your Birthday (dd/mm/yyyy)</Form.Label>
            <InputGroup className='mb-3'>
              <FormControl
                placeholder='Day'
                aria-label='Day'
                aria-describedby='basic-addon3'
                onChange={(event) => {
                  this.setState({ day: event.target.value });
                }}
              />
              <FormControl
                placeholder='Month'
                aria-label='Month'
                aria-describedby='basic-addon3'
                onChange={(event) => {
                  this.setState({ month: event.target.value });
                }}
              />
              <FormControl
                placeholder='Year'
                aria-label='Year'
                aria-describedby='basic-addon3'
                onChange={(event) => {
                  this.setState({ year: event.target.value });
                }}
              />
            </InputGroup>
          </Container>
          <Button variant='primary' onClick={this.handleSubmit} style={{
            fontSize: '40px',
            fontWeight: 'bolder'
          }}>Submit</Button>
        </Container>
      </Fragment >
    );
  }

  handleSubmit = () => {
    const { firstName, lastName, day, month, year } = this.state;
    let numDay = parseInt(day);
    if (isNaN(numDay)) {
      console.log('invalid day');
      return false;
    }
    let numMonth = parseInt(month);
    if (isNaN(numMonth)) {
      console.log('invalid month');
      return false;
    }
    let numYear = parseInt(year);
    if (isNaN(numYear)) {
      console.log('invalid year');
      return false;
    }
    let date = new Date(year, month - 1, day);
    console.log(date.toString());
    // actually submit it
    let db = new Kinto();
    let birthdays = db.collection('birthdays');

    return true;
  }

}

export default BirthdaySubmission;