import React, { Component, Fragment } from 'react';
import Badge from 'react-bootstrap/Badge'
import NavBar from './components/navBar';
import CountdownDisplay from './Pages/Countdown/countdownDisplay';
import BirthdayList from './Pages/BirthdayList/birthdayList';
import { HashRouter, Route, Routes } from 'react-router-dom';
import { timeBetween } from './helperFunctions';
import BirthdaySubmission from './Pages/BirthdaySubmission/BirthdaySubmission';

export class Birthday extends Date {
  constructor(date, firstName, lastName) {
    super(date);
    this.firstName = firstName;
    this.lastName = lastName;
  }
}

class App extends Component {

  state = {
    birthdays: [new Birthday('Dec 23, 1993', 'Joe', 'Smith')]
  }

  constructor(props) {
    super(props);
    this.addBirthday(new Birthday('Feb 8, 2007', 'Jason', 'Zhu'));
    this.addBirthday(new Birthday('Apr 3, 2006', 'Cleo', 'Knight'));
    this.addBirthday(new Birthday('Mar 16, 2001', 'Pierre', 'Martin'));
    this.addBirthday(new Birthday('Aug 3, 2007', 'Pat', 'Thrussell'));

    // get the birthdays from the database here

    const now = new Date();
    this.state.birthdays.sort((a, b) => {
      let aYear = a.getFullYear();
      let bYear = b.getFullYear();

      a.setFullYear(now.getFullYear());
      b.setFullYear(now.getFullYear());
      if (a - now < 0) {
        a.setFullYear(a.getFullYear() + 1);
      }
      if (b - now < 0) {
        b.setFullYear(b.getFullYear() + 1);
      }

      let aDis = Math.floor(timeBetween(a, now, 1000 * 60 * 60 * 24));
      let bDis = Math.floor(timeBetween(b, now, 1000 * 60 * 60 * 24));

      a.setFullYear(aYear);
      b.setFullYear(bYear);
      return aDis - bDis;
    });

  }

  render() {
    return (
      <Fragment>

        <NavBar />

        <HashRouter>
          <Routes>
            <Route
              path='/countdown'
              element={< CountdownDisplay birthdays={this.state.birthdays} />}>
            </Route>
            <Route
              path='/list'
              element={<BirthdayList birthdays={this.state.birthdays} />}>
            </Route>
            <Route
              path='/birthday-submission'
              element={<BirthdaySubmission />}
            >
            </Route>
            <Route
              path='/'
              element={< CountdownDisplay birthdays={this.state.birthdays} />}>
            </Route>
            <Route
              path='*'
              element={<h1 className='m-2'>404 page not found</h1>}>
            </Route>
          </Routes>
        </HashRouter>

      </Fragment>
    );
  }

  getBadge(n) {
    return <Badge bg='secondary' pill={true}>{Math.floor(this.state.count / n) % 10}</Badge>;
  }

  addBirthday(birthday) {
    let birthdays = this.state.birthdays;
    birthdays.push(birthday);
    this.setState({ birthdays });
  }

}

export default App;