import { Birthday } from "./app";

export function getFormattedDate(date) {

  return date.toLocaleString('en-US', {
    weekday: 'long',
    year: 'numeric',
    month: 'long',
    day: 'numeric'
  })
}
export function timeBetween(a, b, millisPerTime) {
  return (a - b) / millisPerTime;
}